import React, { PureComponent } from 'react';
import {
    LoginWrapper,
    AdminLoginWrapper
} from '../Login/style';

class Reg extends PureComponent {
    render() {
        return (
            <LoginWrapper>
                <AdminLoginWrapper className='admin_login'>
                    <dt>
                        <strong></strong>
                        <em></em>
                    </dt>
                    <dd className="user_icon">
                        <input type="text" name="account" placeholder="" className="login_txtbx"/>
                    </dd>
                    <dd className="name_icon">
                        <input type="text" name="username" placeholder="" className="login_txtbx" />
                    </dd>
                    <dd className="email_icon">
                        <input type="text" name="email" placeholder="" className="login_txtbx" />
                    </dd>
                    <dd className="pwd_icon">
                        <input type="password" name="password1" placeholder="" className="login_txtbx" />
                    </dd>
                    <dd className="pwd_icon">
                        <input type="password" name="password2" placeholder="" className="login_txtbx" />
                    </dd>
                    <dd className="val_icon">
                        <div className="checkcode">
                            <input type="text" id="J_codetext" placeholder="" maxLength="4" className="login_txtbx" name="code" />
                            <img id="code_img" src="http://www.yuxi-paper.com/api.php?op=checkcode&amp;code_len=4&amp;font_size=20&amp;width=110&amp;height=50&amp;font_color=&amp;background=" />
                        </div>
                        <input type="button" value="" className="ver_btn" />
                    </dd>
                    <dd>
                        <input type="submit" value="" className="submit_btn" />
                    </dd>
                    <dd>
                        <p></p>
                        <p>byzhkc3@gmail.com</p>
                    </dd>
                </AdminLoginWrapper>
            </LoginWrapper>
        )
    }
}

export default Reg;


